@media screen and (min-width: 768px){
.mobile-triger{display: none !important;}
.navigation_bar ul > a{display: none;}
.filter-bar {position: sticky; top: 0;}
.hide-on-web{display: none;}
.filter-bar h3 i{display: none;}
.filter-bar h3 span{display: none;}
}

@media screen and (max-width: 1440px){
.margin-auto {width: 60%;}
}

@media screen and (max-width: 1280px){
