.product-buy a{flex: 0 0 100%; max-width: 100%; margin-bottom: 12px;}
.product-buy a:last-child{margin-bottom: 0;}
.brand img{width: 100%; padding: 0 15px;}
.icon-box {width: 35px; height: 35px; font-size: 18px;}
}

