}
.owl-one button.owl-next {
    position: absolute;
    top: 40%;
    right: 20px;
}
.owl-one .owl-nav button span {
    font-size: 30px;
}
.banner-texT{top: 18%;}
h2.section-heading {
    font-size: 22px;
    margin-left: 0;
    margin-right: 0;
}
.help-center{margin-bottom: 20px;}
.top-45{top: 28% !important;}
.filter-bar {
    position: fixed;
    top: 0;
    left: -100%;
    z-index: 99;
    background: #effafa;
    width: 250px;
    bottom: 0;
    transition: all 400ms;
}
.filter-bar.active {
    left: 0;
}
.after-mobile-convert-tab {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 30px;
}
